@import url(https://fonts.googleapis.com/css?family=Ubuntu);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Ubuntu', sans-serif;
}

body:not(:hover){
    -webkit-transition-property: body;
    -webkit-transition-duration: 0.75s;
    -webkit-transition-timing-function: ease;
    transition-property: body;
    transition-duration: 0.75s;
    transition-timing-function: ease;
}

    

.btn-wrapper {
  height: 11vh;
  overflow: hidden;
  display: flex;
  margin: 0;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-weight: bolder;
  font-size: 1.75vw;
  /* Controls the border colour for flexbox */
  outline: 0;
  width: 100%;
  flex: 1 1; }

.btn-wrapper:not(:hover), .btn-wrapper.disabled:hover {
  /* Animations are played on colour change except when currently hovering over enabled buttons */
  -webkit-transition-property: background-color, outline;
  -webkit-transition-duration: 0.75s;
  -webkit-transition-timing-function: ease;
  transition-property: background-color, outline;
  transition-duration: 0.75s;
  transition-timing-function: ease; }

.btn-wrapper.ENT {
  background-color: #26ED58;
  color: black; }

.btn-wrapper.ENT:hover:not(.disabled) {
  background-color: #0FBD3B; }

.btn-wrapper.DEL {
  background-color: #EE2B2B;
  color: white; }

.btn-wrapper.DEL:hover:not(.disabled) {
  background-color: #BD0F0F; }

.btn-wrapper.DEL, .btn-wrapper.ENT {
  height: 22vh; }

.btn-wrapper.UNDO, .btn-wrapper.REDO {
  width: 50%;
  margin-bottom: 4vh;
  margin-left: 25%; }

.btn-wrapper.orange {
  background-color: #FE9241;
  color: white;
  outline: 1px solid #ED7728; }

.btn-wrapper.orange.UNDO, .btn-wrapper.orange.REDO {
  outline: 3px solid #ED7728; }

.btn-wrapper.orange:hover:not(.disabled) {
  background-color: #ED7728; }

.btn-wrapper.blue {
  background-color: #43A1FE;
  color: white;
  outline: 1px solid #268AED; }

.btn-wrapper.blue.UNDO, .btn-wrapper.blue.REDO {
  outline: 3px solid #268AED; }

.btn-wrapper.pink:hover:not(.disabled) {
  background-color: #ED268A; }

.btn-wrapper.pink {
  background-color: #FE43A1;
  color: white;
  outline: 1px solid #ED268A; }

.btn-wrapper.pink.UNDO, .btn-wrapper.pink.REDO {
  outline: 3px solid #ED268A; }

.btn-wrapper.pink:hover:not(.disabled) {
  background-color: #ED268A; }

.btn-wrapper.disabled {
  background-color: #C0C0C0;
  color: white; }

.btn-wrapper.disabled.UNDO, .btn-wrapper.disabled.REDO {
  outline: 3px solid #888; }

@media (orientation: portrait) {
  .btn-wrapper {
    font-size: 3vh; } }

.input {
  display: flex;
  padding-left: 0.75vw;
  justify-content: left;
  align-items: center;
  font-weight: bolder;
  font-size: 3vw;
  outline: 1px solid #888;
  flex: 1 1;
  width: 100%;
  height: 100%;
  min-height: 3vh;
  -webkit-transition-property: background-color;
  -webkit-transition-duration: 0.75s;
  -webkit-transition-timing-function: ease;
  transition-property: background-color;
  transition-duration: 0.75s;
  transition-timing-function: ease; }

.input.orange {
  background-color: #ED7728;
  color: white;
  outline: 1px solid #ED7728; }

.input.blue {
  background-color: #268AED;
  color: white;
  outline: 1px solid #268AED; }

.input.pink {
  background-color: #ED268A;
  color: white;
  outline: 1px solid #ED268A; }

@media (orientation: portrait) {
  .input {
    font-size: 6vh; } }

table {
  width: 100%;
  height: 66vh;
  border-collapse: collapse; }

td {
  width: 25%;
  height: 11vh;
  padding: 0;
  webkit-transition-property: background-color;
  -webkit-transition-duration: 0.75s;
  -webkit-transition-timing-function: ease;
  transition-property: background-color;
  transition-duration: 0.75s;
  transition-timing-function: ease; }

td.orange {
  background-color: #FE9241; }

td.blue {
  background-color: #43A1FE; }

td.pink {
  background-color: #FE43A1; }

.player {
  width: 50%;
  height: 30vh;
  float: left;
  webkit-transition-property: background-color, color;
  -webkit-transition-duration: 0.75s;
  -webkit-transition-timing-function: ease;
  transition-property: background-color, color;
  transition-duration: 0.75s;
  transition-timing-function: ease; }

.player.orange {
  background-color: #FE9241; }

.player.blue {
  background-color: #43A1FE; }

.player.pink {
  background-color: #FE43A1; }

table.header {
  width: 100%;
  height: 25%;
  webkit-transition-property: background-color, color;
  -webkit-transition-duration: 0.75s;
  -webkit-transition-timing-function: ease;
  transition-property: background-color, color;
  transition-duration: 0.75s;
  transition-timing-function: ease; }

td.header {
  width: 33.3333%; }

div.history {
  height: 34vh;
  width: 12.5%;
  z-index: 1;
  text-align: center;
  overflow: auto;
  webkit-transition-property: background-color, color;
  -webkit-transition-duration: 0.75s;
  -webkit-transition-timing-function: ease;
  transition-property: background-color, color;
  transition-duration: 0.75s;
  transition-timing-function: ease; }

div.history.orange {
  background-color: #ED7728; }

div.history.blue {
  background-color: #268AED; }

div.history.pink {
  background-color: #ED268A; }

div.history.playerone {
  float: left;
  margin-right: -12.5%; }

div.history.playertwo {
  float: right;
  margin-left: -12.5%; }

p {
  color: white;
  font-size: 1.25vw;
  padding-top: 1vh; }

h2 {
  color: white;
  text-align: center;
  font-size: 3vw; }

h2.legs.playerone {
  text-align: right;
  padding-right: 0.25em; }

h2.legs.playertwo {
  text-align: left;
  padding-left: 0.25em; }

table.header.orange {
  background-color: #ED7728; }

table.header.blue {
  background-color: #268AED; }

table.header.pink {
  background-color: #ED268A; }

h1 {
  color: white;
  text-align: center;
  font-size: 17vh;
  -webkit-transition-property: color;
  -webkit-transition-duration: 0.75s;
  -webkit-transition-timing-function: ease;
  transition-property: color;
  transition-duration: 0.75s;
  transition-timing-function: ease;
  -webkit-text-stroke: 2px white; }

h1.bad {
  color: #BD0F0F; }

h1.good {
  color: #0FBD3B; }

@media (orientation: portrait) {
  h2 {
    font-size: 4.5vh; }
  h2.legs {
    font-size: 6vh; }
  h1 {
    font-size: 16vh; } }

.modal {
    position: fixed;
    text-align: center;
    z-index: 2;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgb(0,0,0);
    background-color: rgba(0,0,0,0.4);
}

.modalContent {
    display: inline-block;
    background-color: #fefefe;
    overflow: auto;
    margin: 5% auto;
    padding: 1.25em;
    border-style: solid;
    border-width: 0.175em;
    border-radius: 0.5em;
    max-width: 85%;
    max-height: 85%;
}

.modalText {
    color: black;
    font-size: 1.5vw;
}

button.formButton, select {
    font-size: 1.5vw;
    width: 100%;
    text-align: center;
    margin-top: 5%;
}

@media (orientation: portrait) {
    .modalText, button.formButton, select {
	font-size: 4vh;
    }
}

